import React, { Component, PropTypes } from 'react';
import { Link } from 'react-router';
// import {
//   Table,
//   TableHeader,
//   TableBody,
//   TableRow,
//   TableHeaderColumn,
//   TableRowColumn,
//   TableRowColumnActions,
// } from './TableResizable';

import {
  Table,
  TableHeader,
  TableBody,
  TableRow,
  TableHeaderColumn,
  TableRowColumn,
  TableRowColumnActions,
  DropdownButton,
  MenuItem,
  Checkbox, Select, Option,
  Popover,
} from '../rlds';   //


class BulkRegistOptions extends Component {
  constructor(props) {
    super(props);
  }

  componentWillMount() {
    this.props.fetchRegisters(this.props.querySettings);
  }

  // renderCategories(categories) {
  //  return categories.map((c) => {
  //     c = c.trim();
  //     return (
  //       <Link to={"filter/" + c} key={c} className="list-group-item-text">{" " + c + " "}</Link>
  //     );
  //  });
  // }

  // renderCards(cards) {
  //   return cards.map((card) => {
  //     return (
  //       <li className="list-group-item" key={card.Id}>
  //         <Link style={{color:'black'}} to={'/cards/' + card.Id}>
  //           <h3 className="list-group-item-heading">{card.Name}</h3>
  //         </Link>
  //           {this.renderCategories(card.company_name ? card.company_name.split(',') : [])}
  //       </li>
  //     );
  //   });
  // }


  //
  selectOption(name, e, value) {
    this.props.selectOption(name, value);
  }

  toggleTodo(name, e, value) {
    this.props.toggleTodo(name);
  }

  setAllTodo(name, e, value) {
    this.props.setAllTodo(!this.props.createTodoAll);
    // this.setState({ createTodo: !this.state.createTodo });
  }

  render() {
    if (this.props.loading || !this.props.registers || !this.props.svconfig) {
      return <div></div>
    }

    // ToDo 2017.08.xx V1.20.2
    const todoLabelCreate = this.props.svconfig.todoLabelCreate;
    const ncLabels =  this.props.svconfig.labels.namecard;

    let rows;
    if (this.props.registers) {
      rows = this.props.registers.map((item, rowidx) => {
        let opts;
        opts = item.options.map((opt, aIdx) => {
          let index = aIdx * 10000;         // 10000   
          if (opt.type !== 4) {
            let acnt = opt.optLabel + "/";
            return opt.contOptions.map((cont, cIdx) => <Option key={aIdx+'-'+cIdx}
                value={index+cIdx}>{acnt + cont.optLabel}</Option>);
          }
          else {
            return <Option key={aIdx} value={index}>{opt.optLabel}</Option>;
          }
        });
        return (
          <TableRow key={rowidx}>
            <TableRowColumn>{item.card.company_name}</TableRowColumn>
            <TableRowColumn>{item.card.division}</TableRowColumn>
            <TableRowColumn>{item.card.title_name}</TableRowColumn>
            <TableRowColumn>
              <span className="slds-text-body--regular" style={{ fontWeight: "bolder" }}>
                {item.card.name}
              </span>
            </TableRowColumn>
            <TableRowColumn>{item.card.displayAddress}</TableRowColumn>
            <TableRowColumn>{item.card.mobile}</TableRowColumn>
            <TableRowColumn>{item.card.email}</TableRowColumn>
            <TableRowColumn>
              <Select onChange={ this.selectOption.bind(this, rowidx) } > {opts} </Select>
            </TableRowColumn>
            <TableRowColumn className="slds-text-align--center"><Checkbox checked={item.createTodo} onChange={ this.toggleTodo.bind(this, rowidx) }/></TableRowColumn>
          </TableRow>
          );
      });
    }

    const styles = { padding: '12px' };
    return (
      <div style={ styles } className="slds-scrollable">
        <Table
          bordered
          striped
        >
          <TableHeader>
            <TableRow>
              <TableHeaderColumn resizable={ true } >{ ncLabels.company }</TableHeaderColumn>
              <TableHeaderColumn resizable={ true } >{ ncLabels.division }</TableHeaderColumn>
              <TableHeaderColumn resizable={ true } width={50}>{ ncLabels.title }</TableHeaderColumn>
              <TableHeaderColumn resizable={ true } >{ ncLabels.name }</TableHeaderColumn>
              <TableHeaderColumn resizable={ true } >{ ncLabels.address }</TableHeaderColumn>
              <TableHeaderColumn resizable={ true } >{ ncLabels.mobile }</TableHeaderColumn>
              <TableHeaderColumn resizable={ true } >{ ncLabels.email }</TableHeaderColumn>
              <TableHeaderColumn resizable={ true } width={150}>{ this.props.svconfig.labels.accountRegistTo }</TableHeaderColumn>
              <TableHeaderColumn resizable={ true } ><Checkbox label={ todoLabelCreate } checked={ this.props.createTodoAll } onChange={ this.setAllTodo.bind(this) }/></TableHeaderColumn>
            </TableRow>
          </TableHeader>

          <TableBody>
          {rows}
          </TableBody>
        </Table>
      </div>
    );
  }
}

BulkRegistOptions.propTypes = {
  fetchRegisters: PropTypes.func.isRequired,
  selectOption: PropTypes.func.isRequired,
  toggleTodo: PropTypes.func.isRequired,
  setAllTodo: PropTypes.func.isRequired,
  registers: PropTypes.arrayOf(PropTypes.object).isRequired,
  loading: PropTypes.bool,
  svconfig: PropTypes.object,
  createTodoAll: PropTypes.bool,
};

export default BulkRegistOptions;



