// BulkRegistOptionsContainer.js
// 
import React, { Component } from 'react';
import { connect } from 'react-redux';
// import { fetchPosts, resetDeletedPost, deletePost, deletePostSuccess, deletePostFailure } from '../actions/index';
import { addError, // showModalNotification,
    changeQuerySettings, // 2018.02.xx v1.22
    fetchRegisters, fetchRegistersSuccess, fetchRegistersFailure,
    selectOption, toggleTodo, setAllTodo, } from '../actions/index';
// import Header from '../components/header.js';
import BulkRegistOptions from '../components/BulkRegistOptions.js';
import { FormattedMessage } from 'react-intl';  // v2.0.4 i18n

function mapStateToProps(state) {
  const { loading } = state.get('process').toObject(); // 2018.02.xx v1.22
  const svconfig = state.getIn(['config', 'svconfig']);
  const registers = state.getIn(['registers', 'registers']);
  const createTodoAll = state.getIn(['registers', 'createTodoAll']);
  const querySettings = state.getIn(['config', 'querySettings']);// 2018.02.xx v1.22
  return { registers, createTodoAll, svconfig, querySettings, loading };
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    onDeleteClick: () => {
      dispatch(deletePost(ownProps.postId))
      .then((response) => {
          !response.error ? dispatch(deletePostSuccess(response.payload)) : dispatch(deletePostFailure({ ...response.payload, goback:true }));
        });
    },
    resetMe: () =>{
      dispatch(resetDeletedPost());
    },

    // 
    fetchRegisters: (querySettings) => {
      // 
      if (!vfValue || !vfValue.targetIds || !Array.isArray(vfValue.targetIds) || vfValue.targetIds.length == 0) {
        //  
        const content = { title: <FormattedMessage id="caption_confirm"/>, level: "alert",
                message: <FormattedMessage id="caption_no_selection" />, goback:true };
        dispatch(addError(content));
        return;
      }
      // // 
      // let querySettings = {
      //   bulkRegist: true,
      //   queryType: 0,
      //   querySite: false,
      //   queryState: false,
      //   queryCity: false,
      //   queryZip: false,
      //   acntRecTypeIds: [],
      //   contRecTypeIds: [],
      // };
      querySettings.bulkRegist = true;
      dispatch(changeQuerySettings(querySettings));
      dispatch(fetchRegisters({ cardIds: vfValue.targetIds, querySettings })).then((response) => {
        return !response.error ? dispatch(fetchRegistersSuccess(response.payload)) : dispatch(fetchRegistersFailure({ ...response.payload, goback:true }));
      });
    },

    //  
    selectOption: (rowIdx, optIdx) => {
      dispatch(selectOption(rowIdx, optIdx));
    },

    // ToDo
    toggleTodo: (rowIdx) => {
      dispatch(toggleTodo(rowIdx));
    },

    setAllTodo: (b) => {
      dispatch(setAllTodo(b));
    },

    fetchViewConfig : () => {
      dispatch(fetchViewConfig()).then((response) => {
        !response.error ? dispatch(fetchViewConfigSuccess(response.payload)) : dispatch(fetchViewConfigFailure(response.payload));
      });
    },
    fetchViews: () => {
      dispatch(fetchViews()).then((response) => {
        // let .data ? response.payload.data : {data: 'Network Error'};
        if (!response.error) {
          let data = response.payload;
          dispatch(fetchViewsSuccess(data));
          dispatch(fetchCards({viewId: data.records[0].value})).then((response) => {
            !response.error ? dispatch(fetchCardsSuccess(response.payload)) : dispatch(fetchCardsFailure(response.payload));
          });
        }
        else {
          dispatch(fetchViewsFailure(response.payload));
        }
      });
    },
    fetchCards : () => {
      dispatch(fetchCards()).then((response) => {
        !response.error ? dispatch(fetchCardsSuccess(response.payload)) : dispatch(fetchCardsFailure(response.payload));
      });
    },
    onChangeView : (id) => {
      dispatch(fetchCards({viewId: id})).then((response) => {
        !response.error ? dispatch(fetchCardsSuccess(response.payload)) : dispatch(fetchCardsFailure(response.payload));
      });
    },
    onSort : (id, col) => {
      dispatch(fetchCards({viewId: id, order: col})).then((response) => {
        !response.error ? dispatch(fetchCardsSuccess(response.payload)) : dispatch(fetchCardsFailure(response.payload));
      });
    },
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(BulkRegistOptions);



