// registers-reducer.js
//  
// @flow
import { Map } from 'immutable'
import {
  FETCH_SVCONFIG_SUCCESS,
  FETCH_REGISTERS, FETCH_REGISTERS_SUCCESS, FETCH_REGISTERS_FAILURE,
  SELECT_OPTION,
  TOGGLE_TODO,
  SET_ALLTODO,
} from '../actions/index';

const INITIAL_STATE = Map({
  registers: [],
  createTodoAll: false,
  error: null, loading: false,
});

export default (state: Object = INITIAL_STATE, action: Object) => {
  let registers;
  // let createTodoAll;
  switch(action.type) {

  // 
  case FETCH_REGISTERS:
    return state.merge(Map({ error: null, loading: true }));
  // case FETCH_REGISTERS_SUCCESS:
  //   return state.merge(Map({ registers: action.payload, error:null, loading: false }));
  case FETCH_REGISTERS_SUCCESS:
  let prevs = state.get('registers');
  if (prevs.length === 0) {
      return state.merge(Map({ registers: action.payload, error:null, loading: false }));
    }
    else {
      registers = action.payload.map((item, idx) =>
            Object.assign({}, item, { createTodo : prevs[idx].createTodo }));
      return state.merge(Map({ registers }));
    }

    case FETCH_REGISTERS_FAILURE:
    return state.merge(Map({ registers: null, error: action.payload, loading: false }));

  // 
  // action.payload.option  10000   
  case SELECT_OPTION:
    let cIndex = action.payload.option % 10000;
    let aIndex = (action.payload.option - cIndex ) / 10000;
    registers = state.get('registers');
    registers = registers.map((card, rowIdx) => {
      if (rowIdx === action.payload.row) {
        card.options = card.options.map((option, a) => {
          option.selected = (aIndex === a);
          if (option.type !== 4) {
             option.contOptions = option.contOptions.map((cont, c) => {
              cont.selected = (option.selected && cIndex === c);
              return cont;
            });
          }
          return option;
        });
      }
      return card;
    });
    return state.merge(Map({ registers }));

  case TOGGLE_TODO: {
    registers = state.get('registers');
    let createTodoAll = true;
    // registers = registers.map((item, idx) =>
    //     idx === action.payload.row ?
    //       Object.assign({}, item, { createTodo : !item.createTodo }) : item );
    // return state.merge(Map({ registers, createTodoAll: false }));
    registers = registers.map((item, idx) => {
      if (idx === action.payload.row) {
        item = Object.assign({}, item, { createTodo : !item.createTodo });
      }
      if (!item.createTodo) {
        createTodoAll = false;
      }
      return item;
    });
    return state.merge(Map({ registers, createTodoAll }));
  }

  case FETCH_SVCONFIG_SUCCESS:
    return state.merge(Map({ createTodoAll: action.payload.createTodo }));

  case SET_ALLTODO:
    registers = state.get('registers');
    registers = registers.map((item) =>
          Object.assign({}, item, { createTodo : action.payload }));
    return state.merge(Map({ registers, createTodoAll: action.payload }));

  default:
    return state;
  }
}



