// BulkRegistHeaderContainer.js
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { changeQuerySettings,
        showModalQuerySettings,
        fetchSVConfig, fetchSVConfigSuccess, fetchSVConfigFailure,
        fetchRegisters, fetchRegistersSuccess, fetchRegistersFailure,
        doRegistAccountContact, doRegistAccountContactSuccess, doRegistAccountContactFailure,
        addError, //showModalNotification,
        setAllowRetry,
      } from '../actions/index';
import BulkRegistHeader from '../components/BulkRegistHeader.js';
import { FormattedMessage } from 'react-intl';  // v2.0.4 i18n

function mapStateToProps(state) {
  const { loading } = state.get('process').toObject();
  const svconfig = state.getIn(['config', 'svconfig']);
  const registers = state.getIn(['registers', 'registers']);
  const request = state.get('request').toObject();
  const querySettings = state.getIn(['config', 'querySettings']);
  const res = { svconfig, registers, request, querySettings, loading };
  return res;
}

const mapDispatchToProps = (dispatch, ownProps) => {

  return {
    // 
    fetchSVConfig : () => {
      dispatch(fetchSVConfig()).then((response) => {
        if (!response.error) {
          dispatch(fetchSVConfigSuccess(response.payload));
          if (!response.payload.enableAccountRegist) {
            // 
            //  
            const content = { title: <FormattedMessage id="caption_confirm"/>, level: "alert",
                  message: <FormattedMessage id="error_insufficient_access" />, goback:true };
            dispatch(addError(content));
          }
        }
        else {
          dispatch(fetchSVConfigFailure({ ...response.payload, goback:true }));
        }
      });
    },

    //   show - hide
    showModalQuerySettings: () => {
      dispatch(showModalQuerySettings(true));
    },

    // 
    fetchRegisters: (querySettings) => {
      dispatch(changeQuerySettings(querySettings));
      dispatch(fetchRegisters({ cardIds: vfValue.targetIds, querySettings })).then((response) => {
        return !response.error ? dispatch(fetchRegistersSuccess(response.payload)) : dispatch(fetchRegistersFailure(response.payload));
      });
    },

    //
    onChangeView : (id) => {
      dispatch(fetchCards({viewId: id})).then((response) => {
        // let data = response.payload.data ? response.payload.data : 'Network Error';
        return  !response.error ? dispatch(fetchCardsSuccess(response.payload)) : dispatch(fetchCardsFailure(response.payload));
      });
    },

  }
}

export default connect(mapStateToProps, mapDispatchToProps)(BulkRegistHeader);



