// BulkRegistSettingContainer.js
// 
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { fetchRecordTypeInfos, fetchRecordTypeInfosSuccess, fetchRecordTypeInfosFailure,
          // selectRecordType, selectOverwrite,
          setAccountParam, setContactParam,
          doRegistAccountContact, doRegistAccountContactSuccess, doRegistAccountContactFailure,
          addError, // onSubmit,
    } from '../actions/index';
import BulkRegistSettings from '../components/BulkRegistSettings.js';
import { FormattedMessage } from 'react-intl';  // v2.0.4 i18n

function mapStateToProps(state) {
  const svconfig = state.getIn(['config', 'svconfig']);
  const recTypes = state.getIn(['config', 'recTypes']);
  const registers = state.getIn(['registers', 'registers']);
  const request = state.get('request').toObject();
  const res = { svconfig, recTypes, registers, request };
  return res;
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    // 
    fetchRecordTypeInfos: (objNames) => {
      dispatch(fetchRecordTypeInfos(objNames)).then((response) => {
        return !response.error ? dispatch(fetchRecordTypeInfosSuccess(response.payload)) : dispatch(fetchRecordTypeInfosFailure({ ...response.payload, goback:true }));
      });
    },
    setRecordType: (objName, recTypeId, recTypeName) => {
      let func = objName === 'Account' ? setAccountParam : setContactParam;
      dispatch(func({
        recTypeId, recTypeName,
      }));
    },

    setOverwrite: (objName, overwrite) => {
      let func = objName === 'Account' ? setAccountParam : setContactParam;
      dispatch(func({
        overwrite,
      }));
    },

    onChangeView : (id) => {
      dispatch(fetchCards({viewId: id})).then((response) => {
        !response.error ? dispatch(fetchCardsSuccess(response.payload)) : dispatch(fetchCardsFailure(response.payload));
      });
    },
    onSort : (id, col) => {
      dispatch(fetchCards({viewId: id, order: col})).then((response) => {
        !response.error ? dispatch(fetchCardsSuccess(response.payload)) : dispatch(fetchCardsFailure(response.payload));
      });
    },

    // 2018.03.xx v1.22  Footer 
    //  
    onSubmit: (registers, request, btn) => {
      let targets =[];
      registers.forEach((item, m) => {      // 
        let target = {}
        target.cardId = item.card.id;       // ID
        target.createTodo = item.createTodo;  // ToDo
        // 
        if (item.card.card_exchange_date) {
          target.todoDate = new Date(item.card.card_exchange_date);
        }
        else {
          target.todoDate = new Date();
        }
        item.options.forEach((opt, n) => {  // 
          if (opt.selected) {
            if (opt.type===1) {
              target.acntId = opt.id;
              target.contId = null;
              opt.contOptions.forEach((cont, k) => {  // 
                if(cont.selected) {
                  target.contId = cont.id || null;
                }
              });
            }
            else if (opt.type===3) {
              target.acntId = null;
              target.contId = null;
            }
            else if (opt.type===4) {
              target.none = 1;
            }
          }
        });
        if (!target.none) {
          targets.push(target);
        }
      });
      if (targets.length === 0) {
        //  
        const content = { title: <FormattedMessage id="caption_confirm"/>, level: "alert",
                message: <FormattedMessage id="caption_no_regist_target" />, goback:true };
        dispatch(addError(content));
        btn.disabled = false;    // v1.22 2018.03.xx 
        return;
      }
      dispatch(doRegistAccountContact({ targets, ...request }))
      .then((response) => {
          if (!response.error) {
            // 
            dispatch(doRegistAccountContactSuccess(response.payload));
            if (response.payload.success) {
              if( (typeof sforce != 'undefined') && (sforce != null) ) {
                // LEX or S1
                sforce.one.back(true);
              }
              else {
                // 
                window.open(vfValue.retUrl, "_top");
              }
            }
            else {
              // 
              // let message = response.payload.error || response.payload.message;
              // let error = { title: "", level: "warning", message };
              // error.html = response.payload;
              // dispatch(addError(error));
              // 
              let message = response.payload.message || "";
              const content = { title: response.payload.title, level: "warning", message, apexresult: response.payload, goback:false };
              // const notify =  true;
              dispatch(addError(content));
              // dispatch(showModalNotification(true));
              // if (response.payload.allowRetry) {
              //   dispatch(setAllowRetry(true));
              // }
              btn.disabled = false;    // v1.22 2018.03.xx 
            }
          }
          else {
            // 
            dispatch(doRegistAccountContactFailure(response.payload));
            // dispatch(showModalNotification(true));
            btn.disabled = false;    // v1.22 2018.03.xx 
          }
        });
    },
    //   
    goBack : () => {
      if( (typeof sforce != 'undefined') && (sforce != null) ) {
        // LEX or S1
        sforce.one.back(true);
      }
      else {
        // 
        window.open(vfValue.retUrl, "_top");
      }
    },
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(BulkRegistSettings);



