// ModalQuerySettings.js
import React, { Component, PropTypes } from 'react';

import {
  Modal,
  Form,
  Input,
  DateInput,
  Picklist,
  PicklistItem,
  FieldSet,
  Button,
  Lookup,
  Checkbox, CheckboxGroup,
  Icon,
} from '../rlds';
import { FormattedMessage } from 'react-intl';  // v2.0.4 i18n

const { Row } = FieldSet;
const { Header, Content, Footer } = Modal;

export default class ModalQuerySettings extends Component {
  constructor(props) {
    super(props);
    this.state = {
      AccountRecTypes: this.props.querySettings.acntRecTypeIds,
      ContactRecTypes: this.props.querySettings.contRecTypeIds,
    };
  }

  //
  componentWillReceiveProps() {
    //  
    if (this.state.AccountRecTypes.length===0 && this.props.recTypes.Account && this.props.recTypes.Account.infos.length > 0) {
      this.props.recTypes.Account.infos.map((info, idx) => {
        this.state.AccountRecTypes.push(info.id);
      });
      this.state.AccountRecTypes.push("all");
    }
    if (this.state.ContactRecTypes.length===0 && this.props.recTypes.Contact && this.props.recTypes.Contact.infos.length > 0) {
      this.props.recTypes.Contact.infos.map((info, idx) => {
        this.state.ContactRecTypes.push(info.id);
      });
      this.state.ContactRecTypes.push("all");
    }
  }

  save(event) {
    let acntRecTypeIds = this.state.AccountRecTypes.indexOf("all")!==-1 ? [] : this.state.AccountRecTypes;
    let contRecTypeIds = this.state.ContactRecTypes.indexOf("all")!==-1 ? [] : this.state.ContactRecTypes;
    // this.props.save({ acntRecTypeIds, contRecTypeIds });
    // 2018.02.xx v1.22 OK 
    let querySettings = this.props.querySettings;
    querySettings.acntRecTypeIds = acntRecTypeIds;
    querySettings.contRecTypeIds = contRecTypeIds;
    if (querySettings.bulkRegist) {
      // 
      this.props.fetchRegisters(querySettings);
    }
    else if (this.props.step === 0) {
      // 
      this.props.fetchAccountOptions(querySettings);
    }
    else if (this.props.step === 1 && this.props.accountId) {
      //  
      this.props.fetchContactOptions(this.props.accountId, querySettings);
    }
    this.props.hideModal();
  }

  colseModal(event) {
    if (event) {
      event.preventDefault();
    }

    this.props.hideModal();
  }

  // 
  onFieldChange(name, recTypes, e, value) {
    if (e.target.value === "all") {
      if (!e.target.checked) {
        // all 
        this.setState({ [name]: [] });
      }
      else {
        // all 
        let values = [];
        recTypes.infos.map((info, idx) => {
          values.push(info.id);
        });
        values.push("all");
        this.setState({ [name]: values });
      }
    }
    else {
      if (value.indexOf("all")!==-1) {
        value.splice(value.indexOf("all"), 1);
      }
      this.setState({ [name]: value });
    }
  }

  getRectypeSelecter(recTypes, selectedIds, disabled) {
    if (!recTypes.infos || recTypes.infos.length === 0) {
      return null;
    }
    let opts = recTypes.infos.map((info, idx) => {
      let b = selectedIds.indexOf(info.id) !== -1;
      return <Checkbox key={idx} label={ info.label } value={ info.id } checked={ b } disabled={ disabled } />;
    });
    if (opts.length > 0) {
      let b = selectedIds.indexOf("all") !== -1;
    opts.push(<Checkbox key={opts.length} label={ <FormattedMessage id="dlg_rectype_all" /> } value="all" checked={ b } disabled={ disabled } />);
    }
    let result =
    <CheckboxGroup label={ <FormattedMessage id="dlg_rectype_obj" values={{ obj: recTypes.objLabel }} /> } name= { `${recTypes.objName}RecTypes` }
                onChange={ this.onFieldChange.bind(this, `${recTypes.objName}RecTypes`, recTypes) }
                disabled={ disabled } >
        {opts}
        </CheckboxGroup>;
    return result;
  }

  render() {
    let okEnable = true;

    // 
    let acntRecTypeSelector;
    if (this.props.recTypes.Account && this.props.recTypes.Account.infos.length > 0) {
      acntRecTypeSelector = this.getRectypeSelecter(this.props.recTypes.Account, this.state.AccountRecTypes, this.props.step !== 0);
      okEnable = this.state.AccountRecTypes.length > 0;
    }
    // 
    let contRecTypeSelector;
    if (this.props.recTypes.Contact && this.props.recTypes.Contact.infos.length > 0) {
      contRecTypeSelector = this.getRectypeSelecter(this.props.recTypes.Contact, this.state.ContactRecTypes, (this.props.step !== 0 && this.props.step !== 1));
      okEnable = okEnable && this.state.ContactRecTypes.length > 0;
    }

    return (
      <div>
        <Modal
          opened={ this.props.opened }
          onHide={ this.colseModal.bind(this) }
        >
          {/*   */}
          <Header title={ <FormattedMessage id="dlg_config_title" /> } closeButton/>
          <Content style={ { padding: '1em' } }>
            <Form type='compound'>
              {/*  */}
              <div className="slds-text-body--regular"><FormattedMessage id="dlg_rectype_caption" /></div>
              <div className="slds-m-left--large">
                <Row>
                  <FieldSet >
                    { acntRecTypeSelector }
                  </FieldSet>
                  <FieldSet>
                    { contRecTypeSelector }
                  </FieldSet>
                </Row>
              </div>
            </Form>
          </Content>
          <Footer directional>
            {/* Cancel  */}
            <Button type='neutral' label={ <FormattedMessage id='btn_cancel'/> } onClick={ this.colseModal.bind(this) } />
            {/* OK  */}
            <Button type='brand' label= { <FormattedMessage id='btn_ok'/> } onClick={ this.save.bind(this) }  disabled={ !okEnable }/>
          </Footer>
        </Modal>
      </div>
    );
  }
}

ModalQuerySettings.propTypes = {
  querySettings: PropTypes.shape({
    bulkRegist: PropTypes.bool,
    queryType: PropTypes.number,
    querySite: PropTypes.bool,
    queryState: PropTypes.bool,
    queryCity: PropTypes.bool,
    queryZip: PropTypes.bool,
    acntRecTypeIds: PropTypes.arrayOf(PropTypes.object),
    contRecTypeIds: PropTypes.arrayOf(PropTypes.object),
  }),
  // save: PropTypes.func,
  fetchRegisters: PropTypes.func.isRequired,
  fetchAccountOptions: PropTypes.func.isRequired,
  fetchContactOptions: PropTypes.func.isRequired,
  hideModal: PropTypes.func,
  recTypes: PropTypes.object,
  opened: PropTypes.bool,
};



