// BulkRegistFooter.js
import React, { Component, PropTypes } from 'react';
import { Link } from 'react-router';
import { Button } from '../rlds';
import { FormattedMessage } from 'react-intl';  // v2.0.4 i18n

class BulkRegistFooter extends Component {
  // constructor(props) {
  //   super(props);
  // }

  // componentWillMount() {

  // }

  cancel() {
    this.props.goBack();
  }

  // submit() {
  //   this.props.onSubmit(this.props.registers, this.props.request);
  // }

  render() {

    return (
      <div style={{height: "180px;"}}>
        <div className="slds-docked-form-footer slds-p-horizontal--x-large">
          {/*  Cancel  */}
          <Button type='neutral' size='small' icon="ban" onClick={ this.cancel.bind(this) }><FormattedMessage id='btn_cancel'/></Button>
          <Button type='brand' size='small' icon="check" iconAlign='right' onClick={ this.props.onSubmit }><FormattedMessage id='btn_save'/></Button>
        </div>
      </div>
      );
  }
}

BulkRegistFooter.propTypes = {
  onSubmit: PropTypes.func.isRequired,
  goBack: PropTypes.func.isRequired,
};

export default BulkRegistFooter



