// BulkRegistHeader.js
import React, { Component, PropTypes } from 'react';
import { Link } from 'react-router';
import {
  Spinner,
  PageHeader,
  PageHeaderHeading,
  PageHeaderHeadingTitle,
  PageHeaderDetail,
  PageHeaderDetailItem,
  PageHeaderDetailBody,
  PageHeaderDetailLabel,
  Icon, Crumb, Button, ButtonGroup, DropdownButton, MenuItem,
  Text, Grid,
} from '../rlds';
import ModalQuerySettings from '../containers/ModalQuerySettingsContainer';
import ModalNotification from '../containers/ModalNotificationContainer';
import BulkRegistFooter from '../components/BulkRegistFooter.js';
import { injectIntl, FormattedMessage } from 'react-intl'; // v2.0.4 i18n

class BulkRegistHeader extends Component {
  constructor(props) {
    super(props);
    // this.state = {selectView: '', selectQuery: 0};
  }

  componentWillMount() {
    this.props.fetchSVConfig();
  }

  fetchRegisters(name, e) {
    // this.props.fetchRegisters(Number(name));
    // 2018.03.xx v.1.22  container 
    let querySettings;
    switch(Number(name)) {
      case 1:
        querySettings = {
          queryType: 1,
          querySite: true,
          queryState: false,
          queryCity: false,
          queryZip: false,
        };
        break;
      case 2:
        querySettings = {
          queryType: 2,
          querySite: true,
          queryState: true,
          queryCity: false,
          queryZip: false,
        };
        break;
      case 3:
        querySettings = {
          queryType: 3,
          querySite: true,
          queryState: true,
          queryCity: true,
          queryZip: false,
        };
        break;
      case 4:
        querySettings = {
          queryType: 4,
          querySite: true,
          queryState: false,
          queryCity: false,
          queryZip: true,
        };
        break;
      default: // 0
        querySettings = {
          queryType: 0,
          querySite: false,
          queryState: false,
          queryCity: false,
          queryZip: false,
        };
        break;
    }
    querySettings.acntRecTypeIds = this.props.querySettings.acntRecTypeIds;
    querySettings.contRecTypeIds = this.props.querySettings.contRecTypeIds;
    querySettings.bulkRegist = true;
    this.props.fetchRegisters(querySettings);
  }

  // onSubmit(event) {
  //   event.target.disabled = "disabled";     // v1.20.1 2017.08.04. 
  //   if (event) {
  //     event.preventDefault();
  //   }
  //   this.props.onSubmit(this.props.registers, this.props.request);
  // }

  render() {
    // if(this.props.loading) {
    //   return <div></div>
    // } else if(!this.props.svconfig ) {
    //   return <div></div>
    // }
    if (this.props.loading || !this.props.registers || !this.props.svconfig) {
      return <div></div>
    }

    // 
    const namecardPrefix = this.props.svconfig.namecardPrefix;
    //  2017.07.xx V1.20.1
    const namecardLabel = this.props.svconfig.namecardLabel;
    const accountLabel = this.props.svconfig.labels.account.objNamePlural;
    const contactLabel = this.props.svconfig.labels.contact.objNamePlural;
    //   <FormattedMessage/>  SLDS,
    const appTitle = this.props.intl.formatMessage({id: 'app_title_bulk'}, {
      acnts: accountLabel,
      conts: contactLabel
    });
    // 
    // 
    const mAcntName = <FormattedMessage id='menu_acnt_name'
                            values={ { acntN: this.props.svconfig.labels.account.name } }/>;
    // 
    const mAcntNameSite = <FormattedMessage id='menu_acnt_name_dept'
                            values={ { acntN: this.props.svconfig.labels.account.name,
                                        site: this.props.svconfig.labels.account.site } }/>;
    // 
    const mAcntNameState = <FormattedMessage id='menu_acnt_name_dept_pref'
                            values={ { acntN: this.props.svconfig.labels.account.name,
                                        site: this.props.svconfig.labels.account.site,
                                        state: this.props.svconfig.labels.account.state } }/>;
    // 
    const mAcntNameCity = <FormattedMessage id='menu_acnt_name_dept_to_city'
                            values={ { acntN: this.props.svconfig.labels.account.name,
                                        site: this.props.svconfig.labels.account.site,
                                        state: this.props.svconfig.labels.account.state,
                                        city: this.props.svconfig.labels.account.city } }/>;
    // 
    const mAcntNameZip = <FormattedMessage id='menu_acnt_name_dept_zip'
                            values={ { acntN: this.props.svconfig.labels.account.name,
                                        site: this.props.svconfig.labels.account.site,
                                        zipcode: this.props.svconfig.labels.account.zipcode } }/>;
    //  
    const { queryType } = this.props.querySettings;
    const count = this.props.registers  ? this.props.registers.length : 0;
    const sortKey = '#';
    let crumView = null;
    if (vfValue.viewId && vfValue.viewLabel) {
      crumView = <Crumb key={1} href={`/${namecardPrefix}?fcf=${vfValue.viewId}`}>{vfValue.viewLabel}</Crumb>;
    }

    //      info={`${count}   ${sortKey} `}
    return(
      <div>
        <PageHeader>
          <PageHeaderHeading
            breadCrumbs={[
              <Crumb key={0} href={`/${namecardPrefix}/o`}>{namecardLabel}</Crumb>,
              crumView,
            ]}
            title = { appTitle }
            figure={
              <Icon
                category="standard"
                icon="account"
                size="large"
              />
            }
            info={ <FormattedMessage id='caption_count' values={ {num: count} }/> }
            rightActions={[
              <DropdownButton key={0} type="icon-more" icon="search" menuAlign="right" >
              {/*  cornflowerblue #6495ed */}
                <MenuItem selected={queryType===0} iconRight={queryType===0 ? "check" : ""} onClick={this.fetchRegisters.bind(this, 0)}>
                  <i className="fa fa-building-o" style={{color: "#6495ed"}} aria-hidden="true"></i>&nbsp;{ mAcntName }
                </MenuItem>
                <MenuItem selected={queryType===1} iconRight={queryType===1 ? "check" : ""} onClick={this.fetchRegisters.bind(this, 1)}>
                  <i className="fa fa-sitemap" style={{color: "#6495ed"}} aria-hidden="true"></i>&nbsp;{ mAcntNameSite }
                </MenuItem>
                <MenuItem selected={queryType===2} iconRight={queryType===2 ? "check" : ""} onClick={this.fetchRegisters.bind(this, 2)}>
                  <i className="fa fa-map-signs" style={{color: "#6495ed"}}></i>&nbsp;{ mAcntNameState }
                </MenuItem>
                <MenuItem selected={queryType===3} iconRight={queryType===3 ? "check" : ""} onClick={this.fetchRegisters.bind(this, 3)}>
                  <i className="fa fa-location-arrow" style={{color: "#6495ed"}}></i>&nbsp;{ mAcntNameCity }
                </MenuItem>
                <MenuItem selected={queryType===4} iconRight={queryType===4 ? "check" : ""} onClick={this.fetchRegisters.bind(this, 4)}>
                  <i className="fa fa-envelope-o" style={{color: "#6495ed"}}></i>&nbsp;{ mAcntNameZip }
                </MenuItem>
              </DropdownButton>,

              <ButtonGroup key={1} className="slds-button-space-left">
                {/*   */}
                <Button type="icon-border" icon="filterList" onClick={ this.props.showModalQuerySettings.bind(this) }/>
                {/*<Button type="icon-border" icon="filterList" />
                <DropdownButton type="icon-more" icon="sort" menuAlign="right">
                  <MenuItem>Menu Item #1</MenuItem>
                  <MenuItem>Menu Item #2</MenuItem>
                  <MenuItem>Menu Item #3</MenuItem>
                </DropdownButton>*/}
              </ButtonGroup>,
            ]}
          />
        </PageHeader>
        {/*  */}
        <ModalQuerySettings />
        {/*  */}
        <ModalNotification />
        {/*  */}
        {/*<BulkRegistFooter goBack={ this.props.goBack } onSubmit={ this.onSubmit.bind(this) } />*/}
      </div>
      );
  }
}

BulkRegistHeader.propTypes = {
  onSubmit: PropTypes.func.isRequired,
  goBack: PropTypes.func.isRequired,
  fetchSVConfig: PropTypes.func.isRequired,
  fetchRegisters: PropTypes.func.isRequired,
  registers: PropTypes.arrayOf(PropTypes.object).isRequired,
  request: PropTypes.object,
  svconfig: PropTypes.object,
  querySettings: PropTypes.shape({
    bulkRegist: PropTypes.bool,
    queryType: PropTypes.number,
    querySite: PropTypes.bool,
    queryState: PropTypes.bool,
    queryCity: PropTypes.bool,
    queryZip: PropTypes.bool,
    acntRecTypeIds: PropTypes.arrayOf(PropTypes.object),
    contRecTypeIds: PropTypes.arrayOf(PropTypes.object),
  }),
  showModalQuerySettings: PropTypes.func,
};

export default injectIntl(BulkRegistHeader);



