import React from 'react';
import ReactDOM from 'react-dom';
import { connect, Provider } from "react-redux";
import { createStore, applyMiddleware } from 'redux';

// import { createStore } from 'redux';
// import { Provider } from 'react-redux';
// import { combineReducers } from 'redux-immutable';
// import dogReducer from './reducers/dog-reducer';
// import * as Immutable from 'immutable';
import { Map } from 'immutable'

import { Router, hashHistory } from 'react-router';
import thunk from 'redux-thunk';
import promise from 'redux-promise';
import createLogger from 'redux-logger';
import { addLocaleData, IntlProvider } from "react-intl"; // i18n
import ja_JP from './locale/ja_JP'; // i18n
import en_US from './locale/en_US'; // i18n
import ja from 'react-intl/locale-data/ja'; // i18n
import en from 'react-intl/locale-data/en'; // i18n
// 
// import reducers from './reducers';
// import routes from './routes';
// 
import reducers from './reducers/indexb';
import routes from './routesb';

// Edge
// const ua = navigator.userAgent.toLowerCase();
// if (ua.indexOf("edge") === -1) {
// vfValue.loaded = true;
// addLocaleData([...ja, ...en]); // i18n
addLocaleData([ja_JP, en_US]);
const logger = createLogger();

const store = createStore(
  reducers,
  Map({}),
  applyMiddleware(thunk, promise, logger)
);

const ConnectedIntlProvider = connect(state => state.get('config').toObject().language)(IntlProvider); // i18n
// const ConnectedIntlProvider = connect(state => state.getIn(['config', 'svconfig', 'sUserLocale']))(IntlProvider); // i18n

ReactDOM.render(
  // <Provider store={createStoreWithMiddleware(reducers)}>
  <Provider store={ store }>
    <ConnectedIntlProvider>
      <Router history={ hashHistory } routes={ routes } />
    </ConnectedIntlProvider>
  </Provider>
  , document.querySelector('.app')
);
// }



