// BulkRegistSettings.js
//  
import React, { Component, PropTypes } from 'react';
import { Link } from 'react-router';
import classnames from 'classnames';
import {
  Button, Form, Input, Textarea, RadioGroup, Radio,
  CheckboxGroup, Checkbox, Select, Option,
  Picklist, PicklistItem,
  DateInput,
  Lookup,
  FieldSet,
} from '../rlds'; //'react-lightning-design-system'; // '../rlds';
import BulkRegistFooter from '../components/BulkRegistFooter.js';
import { FormattedMessage } from 'react-intl';  // v2.0.4 i18n

const Row = FieldSet.Row;


class BulkRegistSettings extends Component {
  constructor(props) {
    super(props);
  }

  componentWillMount() {
    // 
    var objNames = ['Account', 'Contact'];
    this.props.fetchRecordTypeInfos(objNames);
  }

  // renderCategories(categories) {
  //  return categories.map((c) => {
  //     c = c.trim();
  //     return (
  //       <Link to={"filter/" + c} key={c} className="list-group-item-text">{" " + c + " "}</Link>
  //     );
  //  });
  // }

  // renderCards(cards) {
  //   return cards.map((card) => {
  //     return (
  //       <li className="list-group-item" key={card.Id}>
  //         <Link style={{color:'black'}} to={'/cards/' + card.Id}>
  //           <h3 className="list-group-item-heading">{card.Name}</h3>
  //         </Link>
  //           {this.renderCategories(card.company_name__c ? card.company_name__c.split(',') : [])}
  //       </li>
  //     );
  //   });
  // }

  linkHandler(property) {
    window.location.hash = "#property/" + property.property_id;
  }

  // actionHandler(data, value, label) {
  //   if (label === "Delete") {
  //     this.props.onDelete(data);
  //   } else if (label === "Edit") {
  //     this.props.onEdit(data);
  //   }
  // }

  onFieldChange(name, e, value) {
    if (name === "accountRectypes" ) {
      this.props.setRecordType("Account", value, null);
    }
    else if (name === "contactRectypes" ) {
      this.props.setRecordType("Contact", value, null);
    }
    else if (name === "accountOverwrite" ) {
      this.props.setOverwrite("Account", e.target.checked);
    }
    else if (name === "contactOverwrite" ) {
      this.props.setOverwrite("Contact", e.target.checked);
    }
    this.setState({ [name]: value });     //   CheckBox   

  }

  onSubmit(event) {
    event.target.disabled = "disabled";     // v1.20.1 2017.08.04. 
    if (event) {
      event.preventDefault();
    }
    this.props.onSubmit(this.props.registers, this.props.request, event.target);
    // event.target.disabled = "";     // v1.20.1 2017.08.04. 
  }

  render() {
    if (this.props.loading || !this.props.recTypes || !this.props.svconfig) {
      return <div></div>
    }

    const styles = { padding: '20px' };

    // 
    const namecardLabel = this.props.svconfig.labels.namecard.objName;
    const accountLabel = this.props.svconfig.labels.account.objName;
    const contactLabel = this.props.svconfig.labels.contact.objName;

    let accountRectypes;
    let contactRectypes;
    let acntRectypeCount = 0;
    let contRectypeCount = 0;
    if (this.props.recTypes) {
      if (this.props.recTypes.Account) {
        acntRectypeCount = this.props.recTypes.Account.infos.length;  // 
        if (acntRectypeCount > 0) {
          let opts = this.props.recTypes.Account.infos.map((info, n) => <Option key={n} value={info.id}>{info.label}</Option>);
          accountRectypes = <Select label={ this.props.recTypes.Account.objLabel }
              onChange={ this.onFieldChange.bind(this, 'accountRectypes') }
              defaultValue={ this.props.request.acntParam.recTypeId } >
            {opts}
            </Select>;
        }
      }
      if (this.props.recTypes.Contact) {
        contRectypeCount = this.props.recTypes.Contact.infos.length; // 
        if (contRectypeCount > 0) {
          let opts = this.props.recTypes.Contact.infos.map((info, n) => <Option key={n} value={info.id}>{info.label}</Option>);
          contactRectypes = <Select label={ this.props.recTypes.Contact.objLabel }
              onChange={ this.onFieldChange.bind(this, 'contactRectypes') }
              defaultValue={ this.props.request.contParam.recTypeId } >
              {opts}
            </Select>;
        }
      }
    }

    // 2017.06.xx v1.20  
    const hideRectypeSecsion = classnames((!this.props.svconfig.registDispAccountRectype && !this.props.svconfig.registDispContactRectype)
                                   || (acntRectypeCount===0 && contRectypeCount===0) ? "slds-hide" : null);
    // const hideOverwriteSecsion = classnames(this.props.svconfig.registDispOverWriteCheckbox ? null : "slds-hide");
    const hideAccountOverwriteSecsion = classnames("slds-col slds-size--1-of-2 slds-m-horizontal--x-small", this.props.svconfig.registDispAccountOverWriteCheckbox ? "slds-visible" : "slds-hidden");  // 2018.03.xx v1.22
    const hideContactOverwriteSecsion = classnames("slds-col slds-size--1-of-2 slds-m-horizontal--x-small", this.props.svconfig.registDispContactOverWriteCheckbox ?  "slds-visible" : "slds-hidden");  // 2018.03.xx v1.22
    const acntRectypesClass = classnames("slds-col", "slds-size--1-of-2", "slds-m-horizontal--x-small",
                                    !this.props.svconfig.registDispAccountRectype || acntRectypeCount===0 ? "slds-hidden" : "slds-visible");
    const contRectypesClass = classnames("slds-col", "slds-size--1-of-2", "slds-m-horizontal--x-small",
                                    !this.props.svconfig.registDispContactRectype || contRectypeCount===0 ? "slds-hidden" : "slds-visible");

    return (
      <div style={ styles }>
        <Form type='compound'>
          <FieldSet label={ <FormattedMessage id='caption_select_rectype' /> } className={hideRectypeSecsion}>
            <Row>
              <div className="slds-grid slds-nowrap">
                <div className={acntRectypesClass}>
                    {accountRectypes}
                </div>
                <div className={contRectypesClass}>
                    {contactRectypes}
                </div>
              </div>
            </Row>
          </FieldSet>
          <FieldSet label=''>
            <Row>
              <div className="slds-grid slds-nowrap">
                <div className={hideAccountOverwriteSecsion}>
                    <CheckboxGroup label=''>
                      {/*  */}
                      <Checkbox label={ <FormattedMessage id='option_overwrite_obj' values={ { obj: accountLabel, nc: namecardLabel } }/> }
                          value="1" checked={ this.props.request.acntParam.overwrite }
                          disabled={ !this.props.svconfig.enableAccountEdit }
                          onChange={ this.onFieldChange.bind(this, 'accountOverwrite') }/>
                    </CheckboxGroup>
                </div>
                <div className={hideContactOverwriteSecsion}>
                    <CheckboxGroup label='' >
                      {/*  */}
                      <Checkbox label={ <FormattedMessage id='option_overwrite_obj' values={ { obj: contactLabel, nc: namecardLabel } }/> }
                          value="2" checked={ this.props.request.contParam.overwrite }
                          disabled={ !this.props.svconfig.enableContactEdit }
                          onChange={ this.onFieldChange.bind(this, 'contactOverwrite') } />
                    </CheckboxGroup>
                </div>
              </div>
            </Row>
          </FieldSet>
        </Form>
        {/*  */}
        <div style={{ marginBottom: '48px' }}></div>
        {/*  */}
        <BulkRegistFooter goBack={ this.props.goBack } onSubmit={ this.onSubmit.bind(this) } />
      </div>
    );
  }
// ''  ''
}

BulkRegistSettings.propTypes = {
  fetchRecordTypeInfos: PropTypes.func.isRequired,
  // onDelete: PropTypes.func.isRequired,
  // onEdit: PropTypes.func.isRequired,
  setRecordType: PropTypes.func.isRequired,
  setOverwrite: PropTypes.func.isRequired,
  recTypes: PropTypes.object,
  loading: PropTypes.bool,
  svconfig: PropTypes.object,
  createTodoAll: PropTypes.bool,
  request: PropTypes.object,
};

export default BulkRegistSettings;



